import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			tabCurrentPage: 1,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '已发货',
					loadingType: 'more',
					orderList: []
				}
				// ,
				// {
				// 	state: 5,
				// 	text: '审核未通过',
				// 	loadingType: 'more',
				// 	orderList: []
				// }
			],
		};
	},

	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		// options.state = 0;
		this.tabCurrentIndex = +options.state;




		if (options.state == 0) {
			this.loadData()
		}


	},

	methods: {
		show(type, order_id){
			let data = {
				'order_id': order_id,
			}
			if(type == 1){
				data.type = 1
			}else{
				data.type = 2
			}
			
			this.$http.post('/api/order/getInfo', data).then(res => {
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var data = res.data.data
					if(type == 1){
						console.log(data)
						uni.showModal({
							title:'查看物流',
							content: '物流公司：' + data.express_company_title + ';\n快递单号：' + data.express_send_no
						})
					}else{
						uni.showModal({
							title:'订单详情',
							content: data
						})
					}
				}
			}).catch(err => {
				console.log(err);
			})
			
		},
		//获取订单列表
		loadData(source) {
			//这里是将订单挂载到tab列表下
			let _this = this;
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			// navItem.tabCurrentPage = page;
			let page;
			navItem.tabCurrentPage != undefined ? page = navItem.tabCurrentPage : page = 1;
			// console.log()

			if (source === 'tabChange' && navItem.loaded === true) {
				//tab切换只有第一次需要加载数据
				return;
			}
			if (navItem.loadingType === 'loading') {
				//防止重复加载
				return;
			}

			if (navItem.loadingType === 'noMore') {
				//
				return;
			}

			navItem.loadingType = 'loading';

			let data = {
				'status': state,
				'pageNum': navItem.tabCurrentPage != undefined ? navItem.tabCurrentPage : 1
			}
			this.$http.post('/api/order/goods_order_list', data).then(res => {
				console.log(res)
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var data = res.data.data
					// _this.swiperList = data.swiper;
					// console.log(data)
					let list = data.list;
					let pageInfo = data.page
					setTimeout(() => {
						let orderList = list.filter(item => {
							//添加不同状态下订单的表现形式
							item = Object.assign(item, this.orderStateExp(item.state));
							//演示数据所以自己进行状态筛选
							if (state === 0) {
								//0为全部订单
								return item;
							}
							return item.state === state
						});
						orderList.forEach(item => {
							navItem.orderList.push(item);
						})

						//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
						this.$set(navItem, 'loaded', true);

						//判断是否还有数据， 有改为 more， 没有改为noMore 
						if (page < pageInfo.pageSize) {
							navItem.loadingType = 'more';
							navItem.tabCurrentPage = page + 1;
						} else {
							navItem.loadingType = 'noMore';
						}

						console.log(page, _this.tabCurrentPage, navItem)
					}, 600);
				}
			}).catch(err => {
				console.log(err);
			})


		},

		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		editInfo(){
			uni.redirectTo({
				url: '/pages/user/personal'
			})
		},
		//取消订单
		cancelOrder(item) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				let {
					stateTip,
					stateTipColor
				} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip,
					stateTipColor
				})

				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val => val.id === item.id);
				index !== -1 && list.splice(index, 1);

				uni.hideLoading();
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 0:
					stateTip = '已取消';
					stateTipColor = '#909399';
					break;
				case 1:
					stateTip = '待补全';
					stateTipColor = '#909399';
					break;
				case 3:
					stateTip = '待发货';
					break;
				case 2:
					stateTip = '待兑换';
					break;
				case 4:
					stateTip = '已发货';
					break;
				case 5:
					stateTip = '已完成';
					stateTipColor = '#909399';
					break;

					//更多自定义
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	},
}
